import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';

@Injectable({
  providedIn: 'root'
})
export class ProductServiceService {

  constructor(private _fire:AngularFirestore) { }

  addProduct(data:any)
  {
    this._fire.collection("Products").add(data).then(()=>{

      alert("Successfully Added");
    })
  }

  viewProduct()
  {
    return  this._fire.collection("Products")
  }
  viewOrders()
  {
    return this._fire.collection("Orders")
  }


  viewOrderProducts(data:any)
  {

   return this._fire.collection("Orders").doc(data)

  }

  updateOrde(key:any,data:any)
  {
    this._fire.collection("Orders").doc(key).update(data).then(()=>{

      alert("Successfully updated");
    })
  }
}
