import { ProductServiceService } from './../../services/product-service.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-update-orders',
  templateUrl: './update-orders.component.html',
  styleUrls: ['./update-orders.component.scss']
})
export class UpdateOrdersComponent implements OnInit {

  productID :any;
  product :any;
  order :any;
  constructor(private activated :ActivatedRoute, private service :ProductServiceService) { }

  ngOnInit() {

   //get product ket
     this.productID = this.activated.snapshot.paramMap.get('ref');

     this.service.viewOrderProducts(this.productID).valueChanges().subscribe(result=>{

      this.order = result
      this.product= this.order.products
      console.log(this.product)
     })
  }
update(data :NgForm)
{
  alert(data.status)
  console.log(data.value)

  this.service.updateOrde(this.productID,data.value);
}
}
